body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
  min-height: 340vh;
}

a {
  text-decoration: none;
  cursor: pointer;
}

li {
  list-style: none;
}

button:active,
button:focus {
  outline-style: solid;
  outline-color: transparent !important;
}

:root {
  --black: rgba(0, 0, 0, 0.95);
  --white: #fff;
  --black2: #333;
  --white2: #fff;
}

.dark {
  --white: #333;
  --black: rgba(0, 0, 0, 0.65);
  --black2: #fff;
  --white2: #fff;
}

.body::-webkit-scrollbar {
  width: 0px;
}

.body {
  scrollbar-width: none;
}

.video-fone {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 4;
}

.side {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#side1 {
  clip-path: polygon(100% 0, 0% 100%, 0 0);
}

#side2 {
  clip-path: polygon(100% 0, 0% 100%, 100% 100%);
}

.side video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.start-name {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.start-name video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.lible {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 12vw;
  text-align: center;
  font-weight: 800;
  letter-spacing: 3px;
  color: var(--white2);
  background: var(--black);
  mix-blend-mode: multiply;
  padding-top: 30px;
  line-height: 1;
}

.mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  user-select: none;
  mix-blend-mode: screen;
}

.dark .mask {
  filter: invert(1);
  mix-blend-mode: multiply;
}

.toggle {
  position: relative;
  position: fixed;
  top: 30px;
  right: 30px;
  width: 40px;
  height: 40px;
  background: var(--white);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.08);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
  border: none;
  cursor: pointer;
  overflow: hidden;
}

.toggle span {
  position: absolute;
  width: 20px;
  height: 4px;
  background: var(--black2);
  border-radius: 4px;
  transition: 0.5s;
}

.toggle span:nth-child(1) {
  transform: translateY(-7px);
  width: 12px;
  left: 10px;
}

.toggle.active span:nth-child(1) {
  width: 20px;
  transform: translateY(0px) rotate(45deg);
  left: 10px;
  transition-delay: 0.125s;
}

.toggle span:nth-child(2) {
  transform: translateY(7px);
  width: 7px;
  left: 10px;
}

.toggle.active span:nth-child(2) {
  transform: translateY(0px) rotate(315deg);
  width: 20px;
  left: 10px;
  transition-delay: 0.25s;
}

.toggle.active span:nth-child(3) {
  transform: translateX(60px);
}

.btns {
  position: relative;
  position: fixed;
  top: 30px;
  right: 90px;
  width: 40px;
  height: 40px;
  background: var(--white);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.08);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
  border: none;
  cursor: pointer;
  overflow: hidden;
}

.btns ion-icon {
  font-size: 2em;
  color: var(--black2);
}

.menuToggle ion-icon:nth-child(2) {
  display: none;
}

.menuToggle.active ion-icon:nth-child(1) {
  display: none;
}

.menuToggle.active ion-icon:nth-child(2) {
  display: block;
}

.navigation {
  position: fixed;
  top: 0;
  left: -100%;
  width: 25%;
  height: 100%;
  min-width: 280px;
  background: var(--white);
  color: var(--black2);
  height: 100%;
  display: flex;
  justify-content: left;
  align-items: center;
  z-index: 10;
  padding-left: 40px;
  transition: 0.5s linear;
}

.navigation.active {
  left: 0;
}

.navigation li a {
  display: inline-flex;
  margin: 5px 0;
  font-size: 2em;
  font-weight: 900;
  color: var(--black2);
  padding: 5px 20px 5px 20px;
  border-radius: 40px;
}

.dark.navigation {
  background: var(--white);
}

.dark.navigation li a {
  color: var(--black2);
}

.navigation li a:hover {
  color: var(--white);
  background: var(--black2);
}